  background-color: #f98b21;
  transition: background-color 0.3s;
  color: #fff;
  border: 0;
  cursor: pointer; }
  .button:hover {
    background-color: #fa9738; }

.header {
  background-color: #505258;
  position: relative;
  z-index: 2; }
  .header__container {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .header__page-link {
    display: flex;
    height: 100%;
    padding: 0 40px;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    background-color: #79b74d;
    color: #fff;
    text-decoration: none !important; }
  .header__numbers {
    height: 100%;
    display: flex;
    align-items: center;
    position: relative; }
    .header__numbers-ico {
      height: 27px;
      width: 27px;
      background-color: #4d92e0;
      border-radius: 100%;
      margin-right: 15px;
      position: relative; }
      .header__numbers-ico:before {
        content: '';
        display: block;
        height: 10px;
        width: 10px;
        background-image: url("../img/sprite/sprite.png");
        background-size: 85px 1126px;
        background-position: 0 0;
        position: absolute;
        top: 50%;
        left: 50%;
        margin: -5px 0 0 -4px; }
  .header__number {
    color: #fff; }
    .header__number span {
      color: #858684; }
  .header__numbers-dropdown {
    transition: opacity 0.3s; }
    .header__numbers-dropdown_visible {
      visibility: visible !important;
      opacity: 1 !important; }
    .header__numbers-dropdown .header__number {
      margin-left: 20px; }
  .header__numbers-button {
    background: url("../img/icons/ico-arrow.svg") no-repeat center center;
    background-size: 12px auto;
    border: 0;
    height: 27px;
    width: 27px;
    padding: 0;
    transition: transform 0.3s;
    outline: none;
    display: none; }
    .header__numbers-button_active {
      transform: rotate(180deg); }
  .header__bell-link {
    height: 32px;
    width: 32px;
    text-decoration: none;
    background: url("../img//icons/ico-bell.svg") no-repeat center center;
    background-size: 23px auto;
    display: none; }

@media all and (max-width: 1180px) {
  .header .container {
    padding: 0 15px 0 0; } }

@media all and (max-width: 768px) {
  .header .container {
    padding: 0 5px 0 0; }
  .header__numbers-button {
    display: block; }
  .header__bell-link {
    display: block; }
  .header__numbers {
    padding: 0 15px 0 0; }
  .header__numbers-dropdown {
    position: absolute;
    bottom: 0;
    width: 100%;
    transform: translateY(100%);
    background-color: #505258;
    padding: 5px 15px 15px 15px;
    text-align: right;
    visibility: hidden;
    opacity: 0; }
    .header__numbers-dropdown .header__number {
      margin: 0;
      display: block;
      white-space: nowrap;
      margin-bottom: 10px; }
      .header__numbers-dropdown .header__number:last-child {
        margin: 0; }
  .header__numbers-ico {
    display: none; } }

@media all and (max-width: 576px) {
  .header .container {
    padding: 0 14px; }
  .header__page-link {
    padding: 0;
    font-size: 0;
    width: 32px;
    height: 32px;
    background: #79b74d url("../img/icons/ico-out.svg") no-repeat center center;
    background-size: 18px;
    border-radius: 100%; } }

@media all and (max-width: 360px) {
  .header__number {
    font-size: 13px; }
  .header__numbers-button {
    background-size: 10px auto; } }

.footer {
  background-color: #505258;
  padding: 40px 0 50px 0;
  color: #b5b8c3; }
  .footer__container {
    display: flex;
    justify-content: space-between; }
  .footer__section {
    padding-right: 20px; }
    .footer__section:last-child {
      padding: 0; }
  .footer__list {
    margin: 0;
    padding: 0;
    list-style: none; }
  .footer__item {
    padding-bottom: 10px; }
    .footer__item:last-child {
      padding: 0; }
    .footer__item_padding {
      padding-bottom: 20px; }
  .footer__link {
    color: #b5b8c3;
    text-decoration: none !important;
    transition: color 0.3s;
    font-size: 14px;
    display: inline-block; }
    .footer__link:hover {
