@media all and (max-width: 992px) {
  .footer__container {
    flex-wrap: wrap; }
  .footer__section:nth-child(3) {
    padding-right: 0; }
  .footer__section:last-child {
    width: 100%;
    padding-top: 25px;
    display: flex; }
  .footer__section-block {
    padding-right: 20px; }
    .footer__section-block:first-child {
      order: 2; } }

@media all and (max-width: 576px) {
  .footer .container {
    padding: 0 25px; }
  .footer__section {
    width: 100%;
    padding-bottom: 30px;
    flex-wrap: wrap; }
    .footer__section:last-child {
      padding: 0; }
  .footer__section-block {
